body {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}
ul {
  list-style-type: none;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: url("../../fonts/oswaldregular.woff2") format("woff2"), url("../../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: url("../../fonts/oswaldmedium.woff2") format("woff2"), url("../../fonts/oswaldmedium.woff") format("woff");
}
/*@import "global/mixins.less"; */
.page {
  font-family: "Oswald", "Arial", sans-serif;
  line-height: 30px;
  font-size: 18px;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-body {
  padding: 0;
  margin: 0;
  overflow-x: hidden;
}
.typical-button {
  width: 280px;
  height: 40px;
  background: #68b738;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}
.typical-button:focus {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .typical-button {
    margin: 0;
    width: 244px;
    height: 54px;
    background: #68b738;
    font-weight: normal;
    font-size: 20px;
    line-height: 20px;
    text-transform: uppercase;
    color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.page-header {
  max-width: 768px;
  padding: 0;
  margin: 0 auto;
}
.page-header__logo {
  display: block;
  width: 189px;
  height: 52px;
  padding: 0;
  margin: 12px 0 0 20px;
}
.page-header__logo:hover {
  opacity: 0.6;
}
.page-header__logo:focus {
  opacity: 0.3;
}
.page-header__logo-img {
  width: 176px;
  height: 52px;
}
@media (min-width: 768px) {
  .page-header {
    max-width: 92%;
    padding: 0;
    margin: 0 auto 53px;
    display: flex;
    justify-content: space-between;
  }
  .page-header__logo {
    display: block;
    width: 174px;
    height: 52px;
    padding: 0;
    margin: 20px 0 0 0;
  }
